.search-bar {
    border: 1px solid lightgray;
    border-radius: 5px;
    position: relative;
    top: 46%;
    width: 80%;
    text-align: center;
    margin: auto;
    background-color: antiquewhite;
    
}
.search-bar form {
    width: 100%;
    align-items: center;
    margin-top: 10px;   
}

.search-bar button{
    position: inline-block;
    
}
.search-bar input{
    width: 70%;
    margin-bottom: 20px;
    margin-top: 20px;
    border-radius: 7px;
    height:24px;
}
.searchIcon {
    height: 22px;
    margin-bottom: -4px;
    position:relative;
}